import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import Customers from './Customers'
import { BrowserRouter as Router, Switch, Route, Redirect} from 'react-router-dom';
import { Marker, GoogleApiWrapper, InfoWindow } from 'google-maps-react';

import CurrentLocation from './Map';

// const mapStyles = {
//   width: '100%',
//   height: '85%',
// };

class App extends Component {
  constructor(props){
    super(props);

    this.state={
      showingInfoWindow:false,
      activeMarker:{},
      selectedPlace:{},
      stores:[{latitude:47.359423,longitude:-122.021071, name:"Hello"},
      {latitude:43.359423,longitude:-122.021071, name:"HBabe"}]
    }
  }

  // show infowindow when the marker is clicked
  onMarkerClick = (props, marker, e) =>
    this.setState({
      selectedPlace: props,
      activeMarker: marker,
      showingInfoWindow: true
    });

  // close infowindow when the user clicks on the close button 
  onClose = props => {
    if (this.state.showingInfoWindow) {
      this.setState({
        showingInfoWindow: false,
        activeMarker: null
      });
    }
  };

  displayMarkers= () =>{
    return this.state.stores.map((store,index) => {
      return <Marker Key={index} id={index} position={{
        lat: store.latitude,
        lng: store.longitude,
      }}
      onClick={this.onMarkerClick}
      name={store.name}/>
    })
  }

  render() {
    console.log("Host URL"+process.env.PUBLIC_URL);

    // // draw up a boundary (didn't see the boundary being drawn up)
    // const triangleCoords=[
    //   {lat: 25.774, lng: -80.190},
    //   {lat: 18.466, lng: -66.118},
    //   {lat: 32.321, lng: -64.757},
    //   {lat: 25.774, lng: -80.190}
    // ];

    return (

      <Router basename={process.env.PUBLIC_URL}>
        <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h1 className="App-title">Simple React App</h1>
        </header>

        <div id='map'>

        <CurrentLocation
          centerAroundCurrentLocation
          google={this.props.google}
        >

          
          {/* <Map
            google={this.props.google} 
            zoom={8}
            style={mapStyles}
            initialCenter={{lat: this.state.latitude, lng: this.state.longitude}}
            > */}
              {/* {this.displayMarkers()} */}

              <Marker onClick={this.onMarkerClick} name={'current location'} />

              <InfoWindow
                marker={this.state.activeMarker}
                visible={this.state.showingInfoWindow}
                onClose={this.onClose}
              >
                <div>
                  <h4>{this.state.selectedPlace.name}</h4>
                </div>
              </InfoWindow>
              </CurrentLocation>

              {/* <Polygon
                paths={triangleCoords}
                strokeColor="#0000FF"
                strokeOpacity={0.8}
                strokeWeight={2}
                fillColor="#0000FF"
                fillOpacity={0.35}
              /> */}

          {/* </Map> */}

        </div>

          <Switch>
                <Route exact path= "/" render={() => (
                  <Redirect to="/customerlist"/>
                )}/>
                 <Route exact path='/customerlist' component={Customers} />
          </Switch>
      </div>
    </Router>
    );
  }
}

// export default App;
export default GoogleApiWrapper({
  apiKey: ('AIzaSyDj4msBkNl1a8j6I0wUU37FWX7C-OexnxI')
})(App);


